<template>
  <div class="container">
    <Loading :active="isLoading" :z-index="1060"></Loading>
    <div class="mt-4">
    <table class="table align-middle">
      <thead>
        <tr>
          <th>圖片</th>
          <th>商品名稱</th>
          <th>價格</th>
          <th>功能</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td style="width: 200px">
            <div
              :style="{ backgroundImage: `url(${item.imageUrl})` }"
              style="
                height: 100px;
                background-size: cover;
                background-position: center;
              "
            ></div>
          </td>
          <td>
            {{ item.title }}
          </td>
          <td>
            <div class="h5" v-if="item.price === item.origin_price">
              {{ item.price }} 元
            </div>
            <div v-else>
              <del class="h6">原價 {{ item.origin_price }} 元</del>
              <div class="h5">現在只要 {{ item.price }} 元</div>
            </div>
          </td>
          <td>
            <div class="btn-group btn-group-sm">
              <button
                type="button"
                class="btn btn-outline-secondary"
                @click="getProduct(item.id)"
                :disabled="status.loadingItem === item.id || !item.is_enabled"
              >
                <span
                  class="spinner-border spinner-grow-sm"
                  v-if="status.loadingItem === item.id"
                ></span>
                查看更多
              </button>
              <button
                type="button"
                class="btn btn-danger"
                @click="addToCart(item.id)"
                :disabled="status.loadingItem === item.id || !item.is_enabled"
              >
                <span
                  class="spinner-grow spinner-grow-sm"
                  v-if="status.loadingItem === item.id"
                ></span>
                加到購物車
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="text-end">
      <button
        class="btn btn-outline-danger"
        type="button"
        @click="deleteAllCarts"
      >
        清空購物車
      </button>
    </div>

    <table class="table align-middle">
      <thead>
        <tr>
          <th></th>
          <th>品名</th>
          <th style="width: 150px">數量</th>
          <th>單價</th>
        </tr>
      </thead>
      <tbody>
        <!--購物車資料是否存在-->
        <template v-if="cart.carts">
          <tr v-for="item in cart.carts" :key="item.id">
            <td>
              <button
                type="button"
                class="btn btn-outline-danger btn-sm"
                :disabled="status.loadingItem === item.id"
                @click="removeCartItem(item.id)"
              >
                <i class="bi bi-x"></i>
              </button>
            </td>
            <td>
              {{ item.product.title }}
              <div class="text-success" v-if="item.coupon">已套用優惠券</div>
            </td>
            <td>
                <div class="input-group input-group-sm">
                  <!-- <input min="1" type="number" class="form-control" v-model="item.qty"> -->
                  <input
                    type="number"
                    class="form-control"
                    min="1"
                    v-model.number="item.qty"
                    @blur="updateCart(item)"
                  />
                  <div class="input-group-text">
                    {{ item.product.unit }}
                  </div>
                </div>
            </td>
            <td class="text-end">
              <small v-if="cart.final_total !== cart.total" class="text-success"
                >折扣價:
              </small>
              {{ $filters.currency(item.final_total) }}
            </td>
          </tr>
        </template>
      </tbody>
      <tfoot>
        <tr>
          <td colspan="3" class="text-end">總計</td>
          <td class="text-end">{{ $filters.currency(cart.total) }}</td>
        </tr>
        <tr v-if="cart.final_total !== cart.total">
          <td colspan="3" class="text-end text-success">折扣價</td>
          <td class="text-end text-success">
            {{ $filters.currency(cart.final_total) }}
          </td>
        </tr>
      </tfoot>
    </table>
    <div class="input-group mb-3 input-group-sm">
      <input
        type="text"
        class="form-control"
        v-model="coupon_code"
        placeholder="請輸入優惠碼"
      />
      <div class="input-group-append">
        <button
          class="btn btn-outline-secondary"
          type="button"
          @click="addCouponCode"
        >
          套用優惠碼
        </button>
      </div>
    </div>
  </div>

  <div class="my-5 row justify-content-center">
    <Form ref="form" class="col-md-6" v-slot="{ errors }" @submit="createOrder">
      <div class="mb-3">
        <label for="email" class="form-label">Email</label>
        <Field
          id="email"
          name="email"
          type="email"
          class="form-control"
          :class="{ 'is-invalid': errors['email'] }"
          placeholder="請輸入 Email"
          v-model="form.user.email"
        ></Field>
        <ErrorMessage name="email" class="invalid-feedback"></ErrorMessage>
      </div>

      <div class="mb-3">
        <label for="name" class="form-label">收件人姓名</label>
        <Field
          id="name"
          name="姓名"
          type="text"
          class="form-control"
          :class="{ 'is-invalid': errors['姓名'] }"
          placeholder="請輸入姓名"
          v-model="form.user.name"
        ></Field>
        <ErrorMessage name="姓名" class="invalid-feedback"></ErrorMessage>
      </div>

      <div class="mb-3">
        <label for="tel" class="form-label">收件人電話</label>
        <Field
          id="tel"
          name="電話"
          type="tel"
          class="form-control"
          :class="{ 'is-invalid': errors['電話'] }"
          placeholder="請輸入電話"
          v-model="form.user.tel"
        ></Field>
        <ErrorMessage name="電話" class="invalid-feedback"></ErrorMessage>
      </div>

      <div class="mb-3">
        <label for="address" class="form-label">收件人地址</label>
        <Field
          id="address"
          name="地址"
          type="text"
          class="form-control"
          :class="{ 'is-invalid': errors['地址'] }"
          placeholder="請輸入地址"
          rules="required"
          v-model="form.user.address"
        ></Field>
        <ErrorMessage name="地址" class="invalid-feedback"></ErrorMessage>
      </div>

      <div class="mb-3">
        <label for="message" class="form-label">留言</label>
        <textarea
          name=""
          id="message"
          class="form-control"
          cols="30"
          rows="10"
          v-model="form.message"
        ></textarea>
      </div>
      <div class="text-end">
        <button class="btn btn-danger">送出訂單</button>
      </div>
    </Form>
  </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      products: [],
      cartData: {},
      status: {
        LoadingItem: ''
      },
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: ''
      },
      cart: {},
      isLoading: false,
      coupon_code: ''
    }
  },
  methods: {
    getProducts () {
      this.$http
        .get(
          `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products`
        )
        .then((res) => {
          this.products = res.data.products
          this.isLoading = false
        })
        .catch((error) => {
          this.isLoading = false
          this.$httpMessageState(error.response, '錯誤訊息')
        })
    },
    getProduct (id) {
      this.$router.push(`/user/product/${id}`)
    },
    addToCart (id, qty = 1) {
      const cart = {
        product_id: id,
        qty
      }
      this.status.loadingItem = id
      this.$http
        .post(
          `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`,
          { data: cart }
        )
        .then((response) => {
          this.status.loadingItem = ''
          this.isLoading = false
          this.$httpMessageState(response, '加入購物車')
          this.getCart()
        })
        .catch((error) => {
          this.isLoading = false
          this.$httpMessageState(error.response, '錯誤訊息')
        })
    },
    getCart () {
      this.isLoading = true
      this.$http
        .get(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`)
        .then((res) => {
          this.cart = res.data.data
          this.isLoading = false
        })
        .catch((error) => {
          this.isLoading = false
          this.$httpMessageState(error.response, '錯誤訊息')
        })
    },
    deleteAllCarts () {
      this.isLoading = true
      this.$http
        .delete(
          `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/carts`
        )
        .then((response) => {
          this.$httpMessageState(response, '清除購物車')
          this.getCart()
          this.isLoading = false
        })
        .catch((error) => {
          this.isLoading = false
          this.$httpMessageState(error.response, '清除購物車')
        })
    },
    removeItem (id) {
      this.status.loadingItem = id
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart/${id}`
      this.isLoading = true
      this.$http
        .delete(url)
        .then((response) => {
          this.$httpMessageState(response, '移除購物車品項')
          this.status.loadingItem = ''
          this.isLoading = false
          this.getCart()
        })
        .catch((error) => {
          this.isLoading = false
          this.$httpMessageState(error.response, '移除購物車品項')
        })
    },
    updateCart (data) {
      this.isLoading = true
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart/${data.id}`
      const cart = {
        product_id: data.product_id,
        qty: data.qty
      }
      this.$http
        .put(url, { data: cart })
        .then((response) => {
          this.$httpMessageState(response, '更新購物車')
          this.isLoading = false
          this.getCart()
        })
        .catch((error) => {
          this.isLoading = false
          this.$httpMessageState(error.response, '更新購物車')
        })
    },
    addCouponCode () {
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/coupon`
      const coupon = {
        code: this.coupon_code
      }
      this.isLoading = true
      this.$http
        .post(url, { data: coupon })
        .then((response) => {
          this.$httpMessageState(response, '加入優惠券')
          this.isLoading = false
          this.getCart()
        })
        .catch((error) => {
          this.isLoading = false
          this.$httpMessageState(error.response, '加入優惠券')
        })
    },
    createOrder () {
      this.isLoading = true
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/order`
      const order = this.form
      this.$http
        .post(url, { data: order })
        .then((response) => {
          this.$router.push(`/user/checkout/${response.data.orderId}`)
          this.$refs.form.resetForm()
          this.$httpMessageState(response, '建立訂單')
          this.isLoading = false
          this.getCart()
        })
        .catch((error) => {
          this.isLoading = false
          this.$httpMessageState(error.response, '建立訂單')
        })
    }
  },
  created () {
    this.getProducts()
    this.getCart()
  }
}
</script>
